@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: 'Roboto',sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F4F7FC;
  font-size: 16px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

a{
  color: inherit;
}

body {
    width: 100%;
    position: relative;
}

.main-header__wrapper {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1000;
}

.top-navbar {
    width: 100%;
    height: 64px;
    padding: 0 16px;
    display: flex;
    justify-content: flex-start;
    background: #FFFFFF;
    box-shadow: 0px 1px 6px #DBE4F3;
}

.logo__wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.logo__wrapper h6 {
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0.15px;
    color: #424242;
    margin-left: 12px;
}

.navigation .nav__links {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 50px;
}

.nav__links li {
    background: #FFFFFF;
    border-radius: 6px;
    flex: none;
    order: 1;
    align-self: center;
    margin: 16px 8px;
}

.nav__links li a {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.1px;
    color: #616161;
    flex: none;
    order: 0;
    align-self: center;
    padding: 8px 20px;
    position: relative;
}

.nav__links li a::after {
    content: '';
    position: absolute;
    left: calc(50% - 25px/2);
    top: 90.91%;
    bottom: 0%;
    width: 25px;
    height: 2px;
    background-color: #304FFE;
    display: none;
}

.nav__links li a:hover, .nav__links li a.is-active {
    color: #304FFE;
}

.nav__links li a.is-active:after {
    display: block;
}

.user-profile {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: auto;
}

.user-profile span {
    font-size: 12px;
    line-height: 14px;
    color: #424242;
    flex: none;
    order: 1;
    align-self: center;
    margin: 8px;
}

/* Project Section */

.projects__section {
    display: flex;
    position: relative;
    top: 64px;
}

.requirement__section {
    width: 100%;
    margin-left: 248px;
    position: relative;
    padding: 0 40px;
}

.requirement__top {
    width: 100%;
    margin-top: 36px;
    position: relative;
}

.requirement__top .small-text {
    font-size: 10px;
    line-height: 12px;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    color: #757575;
}

.requirement__top>h5 {
    font-weight: normal;
    font-size: 24px;
    line-height: 28px;
    color: #424242;
    margin: 16px 0px;
}

.requirement__top form {
    display: flex;
    align-items: center;
    flex-direction: row;
    margin-top: 27px;
}

.requirement__top form label {
    display: block;
    font-size: 12px;
    margin-bottom: 5px;
    letter-spacing: 0.4px;
    color: #616161;
}

.requirement__top form>div {
    margin-right: 16px;
}

.requirement__top form input {
    background: #FFFFFF;
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    background-position: 95% 45%;
    border: 1px solid #CCD9EE;
    box-sizing: border-box;
    border-radius: 4px;
    padding: 10px 13px;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.25px;
    color: #9E9E9E;
    outline: none;
}

.requirement__top form input::-webkit-input-placeholder {
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.25px;
    color: #9E9E9E;
}

.requirement__top form input:-ms-input-placeholder {
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.25px;
    color: #9E9E9E;
}

.requirement__top form input::-ms-input-placeholder {
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.25px;
    color: #9E9E9E;
}

.requirement__top form input::placeholder {
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.25px;
    color: #9E9E9E;
}

.requirement__top form select {
    border: 1px solid #CCD9EE;
    /* background-color: #FFFFFF; */
    box-sizing: border-box;
    border-radius: 4px;
    padding: 11px 12px;
    font-size: 12px;
    letter-spacing: 0.4px;
    line-height: 14px;
    width: 90px;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYCAYAAADgdz34AAAACXBIWXMAAAsTAAALEwEAmpwYAAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAABbSURBVHgB7ZHBDYBACAQhNkQJlkInnp1QiiXQEXp/RfR1l+wkvBZ2HhABAOaHs1BVGzNv2U5E7GbWnvIlO3b3Q0QuB69/yl8FmaRSXhLcSarln+k/6UMAgLE4AZRZKPFUcAbaAAAAAElFTkSuQmCC);
    background-repeat: no-repeat;
    background-position: 95% 45%;
}
.left-navbar__wrapper {
    position: fixed;
    width: 246px;
    height: 100%;
    background-color: #ffffff;
    box-shadow: 2px 0px 0px #DAE2ED;
    z-index: 10;
}

/* Nav Head */

.nav-head {
    margin-top: 24px;
    border-bottom: 1px solid rgba(115, 143, 179, 0.2);
    padding-left: 24px;
}

.project-info__container {
    display: flex;
    align-items: center;
}

.project-info__container .project-infos {
    margin-left: 16px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.project-title {
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0.15px;
    color: #424242;
    margin-right: 20px;
}

.project-title span {
    display: block;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.4px;
    color: #9E9E9E;
    margin-top: 5px;
}

.project-infos svg {
    width: 24px;
    height: 24px;
    color: #616161;
}

.nav-head form label {
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.4px;
    color: #9E9E9E;
    margin-right: 8px;
}

.nav-head .phase-selector form label {
    margin-right: 15px;
}

.nav-head form select {
    width: 150px;
    border: 0;
    background: transparent;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.4px;
    color: #000000;
    padding: 4px;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABIAAAASCAYAAABWzo5XAAAACXBIWXMAAAsTAAALEwEAmpwYAAAAAXNSR0IArs4c6QAAAARnQU1BAACxjwv8YQUAAABhSURBVHgB7c7RCYAwDEXRBBzAURzFEbKJq8QN3EBH0IlihAql9JV+t70/hZAeQjTqOM4NRWT1ZwF/DlW90+EEli9m3lLMzPYcAi8KV82OnT8WEEH7EIoxR54SUtWH0ajRXkH0Hfjihlm3AAAAAElFTkSuQmCC) no-repeat 95% 50%;
}

.module-selector {
    margin: 18px 0;
}

.phase-selector {
    margin-bottom: 16px;
}

/* Nav Elemnts */

.nav-elements {
    margin-top: 16px;
}

.nav-elements .top, .nav-elements .bottom {
    display: flex;
    flex-direction: column;
}

.nav-elements .bottom {
    position: absolute;
    left: 0%;
    right: 0%;
    bottom: 64px;
}

.left-nav__items li a {
    width: 100%;
    padding: 16px 24px 18px;
    display: block;
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 0.5px;
    color: #616161;
}

.left-nav__items li a svg {
    width: 24px;
    height: 24px;
    color: #9E9E9E;
    margin-right: 12px;
    position: relative;
    top: 6px;
}

.left-nav__items li a:hover, .left-nav__items li a.is-active {
    border-left: 5px solid #304FFE;
    background-color: #EAEBFF;
}
